.product-card {
  padding: 20px 0 10px;
  .catalog-top {
    margin-top: 50px;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      transform: unset !important;
      display: grid;
      grid-template-columns: repeat(5, calc(20%));
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  .tab--active {
    background: #DEA515;
    border-color: #DEA515;
    .catalog-slide__text {
      color: #151719 !important;
    }
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 20px;
    margin-top: 50px;
  }
  .tab__content {
    display: none;
    &--active {
      display: flex;
    }
  }
  &__img {
    position: relative;
    width: 50%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    width: 50%;
    background: #E3E3E3;
    padding-top: 20px;
  }
  .favorite {
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 15px;
    width: 34px;
    height: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E3E3E3;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: #DEA515;
      svg path {
        stroke: #262626;
      }
    }
    svg {
      width: 19px;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  .favorite--active {
    background: #DEA515;
    svg path {
      fill: #262626;
      stroke: #262626;
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #F6F7F8;
    border: 1px solid #E3E3E3;
    padding: 10px 20px;
  }
  &__subtitle {
    font-size: 14px;
    line-height: 120%;
    color: #666666;
  }
  .share {
    position: relative;
    &:hover {
      .share-icon {
        &:after {
          pointer-events: auto;
        }
      }
      .share-hidden {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &-icon {
      position: relative;
      width: 20px;
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 60px;
        height: 100%;
        background: transparent;
        pointer-events: none;
      }
      img {
        position: relative;
        z-index: 2;
        width: 100%;
        cursor: pointer;
      }
    }
    &-hidden {
      position: absolute;
      top: calc(-100% - 16px);
      left: 50%;
      transform: translate(-50%, 0);
      background: #F6F7F8;
      border: 1px solid #151719;
      border-radius: 5px;
      padding: 8px;
      font-size: 14px;
      line-height: 120%;
      white-space: nowrap;
      cursor: pointer;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -5px;
        transform: translate(-50%, 0);
        width: 12px;
        height: 4px;
        background-image: url("../img/arrow2.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
      }
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    padding: 20px 20px 0 20px;
    p {
      font-size: 16px;
      line-height: 120%;
    }
  }
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    padding: 0 20px 20px 20px;
    margin-top: 68px;
  }
  &__checkboxes {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__checkbox {
    position: relative;
    display: flex;
    &:hover {
      label {
        &:before {
          pointer-events: auto;
        }
      }
      .product-card__checkbox-hidden {
        opacity: 1;
        pointer-events: auto;
      }
    }
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
    }
    label {
      position: relative;
      width: 34px;
      height: 34px;
      border-radius: 50%;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 42px;
        height: 42px;
        border-radius: 50%;
      }
      &:before {
        content: '';
        position: absolute;
        top: -8px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 60px;
        height: 42px;
        background: transparent;
        pointer-events: none;
      }
    }
    &-hidden {
      position: absolute;
      top: calc(-100% - 10px);
      left: 50%;
      transform: translate(-50%, 0);
      background: #F6F7F8;
      border: 1px solid #151719;
      border-radius: 5px;
      padding: 8px;
      font-size: 14px;
      line-height: 120%;
      white-space: nowrap;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -5px;
        transform: translate(-50%, 0);
        width: 12px;
        height: 4px;
        background-image: url("../img/arrow2.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
      }
    }
  }
  .white {
    label {
      background: #F4F4ED;
      &:after {
        border: 1px solid #B0B3AF;
      }
    }
  }
  .gray {
    label {
      background: #B0B3AF;
      &:after {
        border: 1px solid #B0B3AF;
      }
    }
  }
  .red {
    label {
      background: #992A28;
      &:after {
        border: 1px solid #992A28;
      }
    }
  }
  &__link {
    font-size: 16px;
    line-height: 120%;
    color: #666666;
    text-decoration: underline;
    text-underline-offset: 3px;
    &:hover {
      color: #262626;
    }
  }
  &__group2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    background: #F6F7F8;
    border: 1px solid #E3E3E3;
    padding: 20px;
  }
  &__price {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
  }
  &__btns {
    flex-grow: 1;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
  }
  .cartcalc {
    display: flex;
    align-items: center;
    border: 1px solid #E3E3E3;
    padding: 10px;
    input {
      width: 40px;
      height: 25px;
      text-align: center;
      background: transparent;
      border: none;
      padding: 0;
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
    }
  }
  .ccalc-plus,
  .ccalc-minus {
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #666666;
    border-radius: 50%;
    cursor: pointer;
  }
  &__btn {
    flex-grow: 1;
    padding-left: 15px;
    padding-right: 15px;
  }
  &__info {
    font-size: 14px;
    line-height: 120%;
    color: #666666;
    padding: 20px;
  }
  .banner {
    background: #E3E3E3;
    margin-top: 20px;
  }
}
.banner {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding: 30px 100px 30px 100px;
  overflow: hidden;
  &__left {
    position: relative;
    z-index: 1;
    width: 50%;
  }
  &__right {
    position: relative;
    z-index: 1;
    width: 50%;
  }
  &__text {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
  }
  &__btn {
    padding-left: 70px;
    padding-right: 70px;
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 140px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 312px;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.product-info {
  padding: 10px 0 60px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__tab {
    min-height: 56px;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    line-height: 120%;
    color: #fff;
    text-transform: uppercase;
    background: #262626;
    border-bottom: 2px solid transparent;
    padding: 5px 10px;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      color: #DEA515;
      border-color: #DEA515;
    }
  }
  &__tab:not(:last-child) {
    border-right: 1px solid #3C3B3B;
  }
  .tab--active {
    background: #DEA515;
    color: #262626 !important;
  }
  &__wrap {
    margin-top: 20px;
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__content-show {
    display: none;
    justify-content: space-between;
    align-items: center;
    background: #3C3B3B;
    padding: 20px;
    cursor: pointer;
    font-size: 14px;
    line-height: 120%;
    color: #fff;
    text-transform: uppercase;
    transition: all 0.3s;
    i {
      width: 14px;
      flex-shrink: 0;
      transition: all 0.3s;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__content-show--active {
    background: #DEA515;
    color: #262626;
    i {
      transform: rotate(-90deg);
      svg path {
        stroke: #262626;
      }
    }
  }
  &__table2 {
    table {
      tbody {
        tr {
          td {
            text-align: start !important;
          }
        }
      }
    }
  }
  table {
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed;
    thead {
      background: #E3E3E3;
      tr {
        th {
          font-size: 14px;
          line-height: 120%;
          font-weight: 700;
          text-transform: uppercase;
          padding: 12px;
          &:not(:last-child) {
            border-right: 1px solid #F6F7F8;
          }
        }
      }
    }
    tbody {
      border: 1px solid #E3E3E3;
      tr {
        &:not(:last-child) {
          border-bottom: 1px solid #E3E3E3;
        }
        td {
          text-align: center;
          font-size: 16px;
          line-height: 120%;
          padding: 20px;
          &:not(:last-child) {
            border-right: 1px solid #E3E3E3;
          }
        }
      }
    }
  }
  &__nav {
    background: #FFFFFF;
    padding: 20px;
  }
  .gray-nav {
    background: #E3E3E3;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
  }
  .subtitle-bg__gray {
    background: #E3E3E3;
    padding: 10px 20px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
    li {
      position: relative;
      font-size: 16px;
      line-height: 120%;
      padding-left: 16px;
      &:after {
        content: '';
        position: absolute;
        top: 6px;
        left: 0;
        width: 5px;
        height: 5px;
        background: #262626;
        border-radius: 50%;
      }
    }
  }
  .diagram {
    &__img {
      background: #fff;
      padding: 20px;
      img {
        max-width: 100%;
      }
    }
  }
  .document {
    display: flex;
    align-items: center;
    gap: 4px;
    &__icon {
      width: 32px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &__title {
      font-size: 14px;
      line-height: 120%;
      color: #107bb1;
      text-decoration: underline;
    }
    &__size {
      font-size: 14px;
      line-height: 120%;
      color: #666666;
    }
  }
  .video {
    position: relative;
    max-width: 558px;
    padding-bottom: 28%;
    margin-top: 20px;
    &-wrap {
      background: #fff;
      padding: 20px;

    }
    &-wrap__gray {
      background: #E3E3E3;
    }
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .banner {
    background: #DEA515;
    margin-top: 50px;
    &__text {
      text-align: center;
    }
    &__btn {
      background: #E3E3E3;
      padding-left: 88px;
      padding-right: 88px;
      &:hover {
        background: #151719;
      }
    }
  }
}
.connection {
  padding: 60px 0;
  &__inner {
    position: relative;
    background: #E3E3E3;
    padding: 50px;
    overflow: hidden;
  }
  &__wrap {
    position: relative;
    z-index: 2;
    max-width: 490px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    margin-top: 10px;
  }
  .form {
    margin-top: 20px;
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: 48px;
    bottom: 0;
    max-width: 525px;
    img {
      max-width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    right: 0;
    bottom: -18px;
    max-width: 645px;
    img {
      max-width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__img3 {
    position: absolute;
    top: 0;
    right: 140px;
    max-width: 535px;
    img {
      max-width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__img4 {
    position: absolute;
    top: 0;
    left: 0;
    max-width: 295px;
    img:nth-child(2) {
      display: none;
    }
  }
  &__img5 {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 315px;
    img:nth-child(2) {
      display: none;
    }
  }
}
.connection2 {
  padding: 60px 0 120px;
  .connection__wrap {
    max-width: 430px;
    margin: 0 auto;
  }
  .connection__title {
    text-align: center;
  }
}

@media screen and (max-width: 1300px) {
  .product-card {
    .share {
      &-hidden {
        left: unset;
        right: 0;
        transform: unset;
        &:after {
          left: unset;
          right: 5px;
          transform: unset;
        }
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .connection {
    &__img {
      max-width: 400px;
    }
    &__img2 {
      right: -100px;
    }
    &__img3 {
      right: 40px;
    }
  }
}

@media screen and (max-width: 992px) {
  .product-card {
    padding: 0 0 10px 0;
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
    &__content {
      margin: 40px auto 0;
    }
    &__group {
      margin-top: 20px;
    }
  }
  .banner {
    flex-direction: column;
    text-align: center;
    padding: 20px;
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      display: flex;
      justify-content: center;
    }
    &__img {
      top: -10px;
      left: -30px;
    }
    &__img2 {
      width: 66px;
      height: auto;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        height: auto;
        display: block;
      }
    }
  }
  .product-info {
    &__top {
      display: none;
    }
    .tab__content {
      display: block;
    }
    &__content-show {
      display: flex;
      border-bottom: 1px solid #6d6d6d;
    }
    &__content-hidden {
      display: none;
    }
    .banner {
      display: none;
    }
  }
  .connection {
    padding: 30px 0 15px;
    &__inner {
      width: calc(100% + 30px);
      padding: 50px 20px;
      margin-left: -15px;
    }
    &__wrap {
      margin: 0 auto;
    }
    &__title {
      text-align: center;
    }
    &__subtitle {
      text-align: center;
    }
    &__img {
      display: none;
    }
    &__img2 {
      right: 0;
      bottom: 0;
      max-width: 115px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__img3 {
      left: 0;
      right: unset;
      max-width: 95px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__img4 {
      left: unset;
      right: 0;
      max-width: 150px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__img5 {
      left: 0;
      right: unset;
      max-width: 106px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
  .connection2 {
    .connection__title {
      text-align: start;
    }
  }
}

@media screen and (max-width: 768px) {
  .product-card {
    &__content {
      flex-direction: column;
    }
    &__img {
      width: 100%;
    }
    &__desc {
      width: 100%;
    }
  }
  .product-info {
    .video {
      max-width: 100%;
      padding-bottom: 56%;
    }
  }
}

@media screen and (max-width: 576px) {
  .product-card {
    .share {
      display: none;
    }
    &__group {
      flex-direction: column;
      align-items: start;
    }
    &__checkboxes {
      flex-direction: column;
    }
    &__checkbox {
      label {
        &:before {
          display: none;
        }
      }
      &-hidden {
        top: 50%;
        left: calc(100% + 10px);
        transform: translate(0, -50%);
        width: fit-content;
        min-width: 154px;
        opacity: 1;
        pointer-events: auto;
        &:after {
          top: 50%;
          left: -10px;
          bottom: unset;
          transform: translate(0, -50%) rotate(90deg);
        }
      }
    }
    &__group2 {
      flex-direction: column;
      align-items: start;
    }
    &__btns {
      width: 100%;
    }
  }
}

@media screen and (max-width: 399px) {
  .product-info {
    table {
      tbody {
        tr {
          td {
            font-size: 14px;
          }
        }
      }
    }
  }
}