.header-top {
  padding: 24px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px
  }
  &__logo {
    width: 192px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px
  }
  &__items {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 14px;
    line-height: 120%;
    color: #666666;
    &:hover {
      color: #DEA515;
      i {
        background: #DEA515;
        svg path {
          stroke: #262626;
        }
      }
    }
    i {
      width: 34px;
      height: 34px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #151719;
      border-radius: 50%;
      transition: all 0.3s;
      svg {
        max-width: 20px;
        height: auto;
      }
    }
  }
  &__items2 {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  &__item2 {
    position: relative;
    &--active {
      .header-top__item2-hidden {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &:hover {
      .header-top__item2-icon {
        background: #DEA515;
        svg path {
          stroke: #262626;
        }
      }
    }
    &-icon {
      width: 34px;
      height: 34px;
      flex-wrap: wrap;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #151719;
      border-radius: 50%;
      cursor: pointer;
      transition: all 0.3s;
    }
    &-show {
      position: absolute;
      z-index: 2;
      top: -5px;
      right: -1.5px;
      width: 15px;
      height: 15px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border: 0.7px solid #151719;
      border-radius: 50%;
      font-size: 10.5px;
      line-height: 105%;
      cursor: pointer;
    }
    &-hidden {
      position: absolute;
      top: -100%;
      left: 50%;
      transform: translate(-50%, 0);
      width: 145px;
      background: #fff;
      border: 0.7px solid #151719;
      border-radius: 4px;
      padding: 6px 5px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -4px;
        transform: translate(-50%, 0);
        width: 9px;
        height: 3px;
        background-image: url("../img/arrow.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
      }
    }
    &-text,
    &-links a {
      font-size: 14px;
      line-height: 120%;
    }
    &-links a:hover {
      color: #DEA515;
    }
    &-close {
      position: absolute;
      top: 3.5px;
      right: 3.5px;
      width: 6px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
  &__btn {
    padding-left: 60px;
    padding-right: 60px;
  }
  .header-bottom__list {
    display: none;
  }
}
.header-bottom {
  background: #151719;
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    li {
      flex-grow: 1;
    }
    li a {
      position: relative;
      min-height: 56px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      line-height: 120%;
      color: #FFFFFF;
      text-transform: uppercase;
      border-bottom: 2px solid transparent;
      padding-left: 12px;
      padding-right: 12px;
    }
    li:not(:last-child) {
      a {
        &:after {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          width: 1px;
          height: 100%;
          background: #3C3B3B;
        }
      }
    }
    li a:hover {
      color: #DEA515;
      border-color: #DEA515;
    }
    .active {
      background: #DEA515;
      color: #151719 !important;
    }
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 40px;
  height: 20px;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #151719;
    border-radius: 4px;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 9px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 9px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1100px) {
  .header-top {
    &__inner {
      gap: 32px;
    }
    &__btn {
      padding-left: 32px;
      padding-right: 32px;
    }
  }
  .header-bottom {
    @media screen and (min-width: 993px) {
      .header-bottom__list {
        li a {
          font-size: 13px;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header-top {
    padding: 10px 0;
    &__right {
      position: fixed;
      z-index: 10;
      top: 80px;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: start;
      align-items: start;
      background: #F6F7F8;
      padding: 0 15px 100px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__items {
      order: 2;
      flex-wrap: wrap;
      align-items: start;
    }
    &__btn {
      order: -1;
    }
    .header-bottom__list {
      position: relative;
      width: 100%;
      display: flex;
      flex-direction: column;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: -15px;
        width: calc(100% + 30px);
        height: 100%;
        background: #151719;
      }
      li {
        position: relative;
        z-index: 2;
      }
      li a {
        justify-content: start;
        padding-left: 27px;
        padding-right: 27px;
        &:after {
          top: unset;
          bottom: 0;
          width: 100%;
          height: 1px;
        }
      }
    }
  }
  .header-bottom {
    display: none;
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 360px) {
  .header-top {
    &__items {
      width: 100%;
      flex-direction: column;
      align-items: center;
    }
    &__wrap {
      width: 100%;
      flex-direction: column;
    }
    &__btn {
      width: 100%;
    }
  }
}