.footer {
  position: relative;
  background: #262626;
  overflow: hidden;
  &__img {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 480px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    left: 0;
    bottom: 0;
    max-width: 650px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.footer-top {
  padding: 50px 0 30px;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 50px;
  }
  &__group {
    .footer-top__btn {
      display: none;
      margin-top: 10px;
    }
  }
  &__logo {
    width: 194px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__btn {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
  &__items {
    max-width: 515px;
    display: flex;
    align-items: start;
    gap: 50px;
    margin-top: 30px;
  }
  &__item {
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
      color: #F1F8FB;
      white-space: nowrap;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 24px;
    li a {
      font-size: 16px;
      line-height: 120%;
      color: #666666;
      &:hover {
        color: #DEA515;
      }
    }
  }
  &__group2 {
    max-width: 270px;
  }
  &__items2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 24px;
  }
  &__item2 {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 16px;
    line-height: 120%;
    color: #F1F8FB;
    &:hover {
      color: #DEA515;
    }
    i {
      width: 34px;
      height: 34px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #DEA515;
      border-radius: 50%;
      transition: all 0.3s;
      svg {
        max-width: 20px;
        height: auto;
      }
    }
  }
  &__search {
    display: flex;
    border: 1px solid #151719;
    margin-top: 24px;
    input {
      width: calc(100% - 44px);
      height: 44px;
      background: #E3E3E3;
      border: none;
      padding: 0 15px;
      font-size: 14px;
      line-height: 120%;
      &::placeholder {
        color: #666666;
      }
    }
    &-btn {
      width: 44px;
      height: 44px;
      background: #DEA515;
      svg {
        width: 24px;
        height: auto;
      }
    }
  }
}
.footer-bottom {
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    border-top: 1px solid #353535;
    padding: 8px 0;
  }
  &__text {
    font-size: 14px;
    line-height: 120%;
    color: #666666;
    a:hover {
      color: #DEA515;
    }
  }
  .brevis {
    display: flex;
    gap: 12px;
    a:hover {
      color: #DEA515;
    }
    span {
      font-size: 14px;
      line-height: 120%;
      color: #666666;
      margin-top: 8px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .footer {
    &__img {
      top: -30px;
      right: -250px;
    }
    &__img2 {
      display: none;
    }
  }
  .footer-top {
    &__items {
      gap: 30px;
    }
    &__item {
      &-title {
        font-size: 20px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer-top {
    padding: 20px 0 50px;
    &__inner {
      flex-direction: column;
    }
    &__group {
      max-width: 194px;
      .footer-top__btn {
        display: flex;
      }
    }
    &__items {
      max-width: 100%;
      justify-content: space-between;
      margin-top: 0;
    }
    &__item {
      &-title {
        font-size: 24px;
      }
    }
    &__btn {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-bottom {
    &__inner {
      flex-direction: column;
      align-items: start;
      padding: 24px 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__group {
      max-width: 100%;
    }
    &__items {
      max-width: 100%;
      flex-wrap: wrap;
    }
    &__item {
      max-width: 140px;
    }
    &__group2 {
      max-width: 100%;
    }
  }
}