.catalog-main {
  position: relative;
  background: #E3E3E3;
  overflow: hidden;
  .container {
    position: relative;
    z-index: 2;
  }
  &__inner {
    position: relative;
  }
  &__desc {
    max-width: 564px;
    padding: 40px 0;
  }
  &__items {
    max-width: 385px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    margin-top: 32px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-icon {
      max-width: 100px;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      margin-top: 5px;
    }
  }
  &__img {
    position: absolute;
    right: 22%;
    bottom: 0;
    max-width: 385px;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 964px;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    top: 0;
    left: 0;
    width: 268px;
    img {
      width: 100%;
    }
  }
}
.catalog {
  padding: 50px 0 60px;
  &2 {
    .catalog__content {
      margin-top: 0;
    }
  }
  &-top {
    position: relative;
    @media screen and (min-width: 993px) {
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(5, calc(20%));
      }
    }
  }
  @media screen and (min-width: 993px) {
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
  &-slide {
    height: auto;
    display: flex;
    align-items: center;
    text-align: center;
    gap: 10px;
    padding: 20px 12px;
    cursor: pointer;
    transition: all 0.3s;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid #E4E4E4;
      pointer-events: none;
    }
    &:hover {
      &:after {
        border-color: #DEA515;
      }
      .catalog-slide__text {
        color: #DEA515;
      }
    }
    &__icon {
      width: 35px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &__text {
      flex-grow: 1;
      font-size: 14px;
      line-height: 120%;
      text-transform: uppercase;
      transition: all 0.3s;
    }
  }
  .tab--active {
    background: #DEA515;
    border-color: #DEA515;
    .catalog-slide__text {
      color: #151719 !important;
    }
  }
  &__content {
    margin-top: 50px;
    @media screen and (min-width: 993px) {
      .swiper-wrapper {
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(4, calc(25% - 60px / 4));
        gap: 20px;
      }
    }
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__content-slider {
    position: relative;
    margin-top: 50px;
  }
}
.catalog-card {
  position: relative;
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 1px solid #E7E7E7;
  padding: 20px;
  &:hover {
    border-color: #262626;
    .catalog-card__title {
      &:after {
        width: calc(100% + 40px);
        background: #DEA515;
      }
    }
  }
  &__img {
    position: relative;
    padding-bottom: 87%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .favorite {
    position: absolute;
    z-index: 1;
    top: 10px;
    right: 10px;
    width: 34px;
    height: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E3E3E3;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: #DEA515;
      svg path {
        stroke: #262626;
      }
    }
    svg {
      width: 19px;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  .favorite--active {
    background: #DEA515;
    svg path {
      fill: #262626;
      stroke: #262626;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
  }
  &__title {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 10px;
    font-size: 14px;
    line-height: 120%;
    color: #666666;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 100%;
      height: 100%;
      background: #F6F7F8;
      pointer-events: none;
      transition: all 0.3s;
    }
    span {
      position: relative;
      z-index: 2;
    }
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    margin-top: 20px;
  }
  &__bottom {
    margin-top: auto;
  }
  &__price {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 20px;
  }
  &__basket {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    background: #E3E3E3;
    padding: 14px;
    margin-top: 20px;
    &-text {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
    }
  }
  .cartcalc {
    display: flex;
    align-items: center;
    input {
      width: 25px;
      height: 25px;
      text-align: center;
      background: transparent;
      border: none;
      padding: 0;
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
    }
  }
  .ccalc-plus,
  .ccalc-minus {
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #666666;
    border-radius: 50%;
    cursor: pointer;
  }
  &__btn {
    width: 100%;
    background: #E3E3E3;
    padding: 18px;
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 20px;
  }
}
.answers {
  padding: 60px 0 120px;
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  @media screen and (min-width: 993px) {
    .swiper-wrapper {
      transform: unset !important;
      display: grid;
      grid-template-columns: repeat(4, calc(25% - 60px / 4));
      gap: 20px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
  }
}
.answer-card {
  border: 1px solid transparent;
  &:hover {
    border-color: #262626;
    .answer-card__title {
      background: #DEA515;
    }
    .answer-card__img {
      img {
        transform: scale(1.2);
      }
    }
  }
  &__title {
    min-height: 85px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #E7E7E7;
    padding: 14px;
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    transition: all 0.3s;
    span {
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:3;
      -webkit-box-orient: vertical;
    }
  }
  &__group {
    position: relative;
  }
  &__subtitle {
    position: absolute;
    z-index: 2;
    top: 10px;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    font-size: 14px;
    line-height: 120%;
    color: #E7E7E7;
    text-transform: uppercase;
  }
  &__img {
    position: relative;
    padding-bottom: 40%;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all 0.3s;
    }
  }
}
.answer-card2 {
  position: relative;
  width: auto !important;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: #E3E3E3;
  grid-column: 3/5;
  padding: 30px 15px;
  margin-right: 0 !important;
  &__mob {
    display: none;
  }
  &__wrap {
    position: relative;
    z-index: 2;
    max-width: 325px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
  }
  &__title {
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: -2px;
  }
  &__btn {
    background: #151719;
    padding: 15px 50px;
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    margin-top: 20px;
    &:hover {
      background: #DEA515;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 120px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 190px;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 1600px) {
  .catalog-main {
    &__img2 {
      right: -300px;
    }
    &__img3 {
      width: 200px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .catalog-main {
    &__img3 {
      width: 100px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .catalog-main {
    &__img {
      right: 10%;
    }
    &__img2 {
      right: -400px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .catalog {
    &-slide {
      &__text {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .catalog-main {
    &__inner {
      display: flex;
      justify-content: center;
    }
    &__desc {
      max-width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      padding: 50px 0;
    }
    &__items {
      margin-top: 20px;
    }
    &__img {
      display: none;
    }
    &__img2 {
      right: -300px;
      bottom: -10px;
      width: 400px;
    }
    &__img3 {
      width: 70px;
    }
  }
  .catalog {
    padding: 20px 0 30px;
    &-top,
    &__content-slider{
      padding: 0 60px;
    }
    .swiper-wrapper {
      align-items: stretch;
    }
    &-slide {
      &__text {
        font-size: 14px;
      }
    }
    .swiper-slide {
      height: auto;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
    &__title {
      text-align: center;
    }
    &__content-slider {
      margin-top: 20px;
    }
  }
  .answers {
    padding: 30px 0 60px;
    &__title {
      text-align: center;
    }
    &__slider {
      padding: 0 60px;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
  }
  .answer-card2 {
    display: none;
    &__mob {
      display: flex;
      margin-top: 60px;
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog-main {
    &__img2 {
      right: -310px;
      bottom: -40px;
    }
    &__img3 {
      top: -10px;
      left: -10px;
    }
  }
  .answer-card2 {
    overflow: hidden;
    &__img {
      top: -15px;
      left: -15px;
    }
    &__img2 {
      height: unset;
      right: -100px;
      bottom: -10px;
    }
  }
}

@media screen and (max-width: 399px) {
  .catalog {
    &-top,
    &__content-slider {
      padding: 0 40px;
    }
    &-slide {
      &__text {
        font-size: 12px;
      }
    }
  }
  .catalog-card {
    &__basket {
      &-text {
        font-size: 12px;
      }
    }
  }
  .answers {
    &__slider {
      padding: 0 40px;
    }
  }
}

@media screen and (max-width: 360px) {
  .catalog-main {
    &__item {
      &-icon {
        max-width: 80px;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
}