.btn-yellow {
  background: #DEA515;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 14px;
  line-height: 120%;
  font-weight: 700;
  color: #262626 !important;
  text-transform: uppercase;
  &:hover {
    background: #151719 !important;
    color: #fff !important;
  }
}
.breadcrumbs {
  padding: 20px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 8px;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 14px;
    line-height: 120%;
    color: #151719;
    text-decoration: underline;
    &--active {
      color: #666666;
      text-decoration: unset;
    }
    &:hover {
      text-decoration-color: transparent;
    }
    span {
      width: 10px;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
  }
}
.text44 {
  font-size: 44px;
  line-height: 120%;
  text-transform: uppercase;
  letter-spacing: -2px;
}
.swiper-button-prev,
.swiper-button-next {
  z-index: 1;
  width: 40px;
  height: 40px;
  border: 1px solid #1E1E1E;
  border-radius: 50%;
  transition: all 0.3s;
  &:after {
    display: none;
  }
  &:hover {
    border-color: #DEA515;
    svg path {
      fill: #DEA515;
    }
  }
  svg {
    width: 32px;
    height: auto;
    transition: all 0.3s;
  }
}
.swiper-button-prev {
  svg {
    transform: rotate(180deg);
  }
}
.calculation {
  top: calc(50% - 120px) !important;
}
.search {
  &-input {
    width: calc(100% - 89px);
    display: flex;
    align-items: center;
    background: #DEA515;
    input {
      width: 100%;
      height: 40px;
      font-size: 16px;
      line-height: 120%;
      background: #ECECEC;
      border: none;
      padding: 0 10px;
    }
  }
}
.fixed-item {
  position: fixed;
  z-index: 5;
  top: 50%;
  right: -170px;
  transform: translate(0, -50%);
  width: 260px;
  height: 90px;
  display: flex;
  align-items: stretch;
  border: 1px solid #DEA515;
  border-right: unset;
  padding: 10px 0 10px 10px;
  transition: all 0.3s;
  .fixed-item__show {
    width: 90px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #DEA515;
    cursor: pointer;
    transition: all 0.3s;
  }
  .calculation__text {
    width: calc(100% - 90px);
    display: flex;
    justify-content: center;
    align-items: center;
    background: #DEA515;
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    padding: 5px 18px 5px 0;
  }
}
.fixed-item--active {
  right: 0;
  border-color: #151719;
  .fixed-item__show {
    background: #151719;
    svg path {
      stroke: #fff;
    }
  }
  .calculation__text,
  .search-input {
    background: #151719;
  }
}
.form {
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
  &-group {
    position: relative;
    input {
      width: 100%;
      height: 46px;
      background: #E3E3E3;
      border: 1px solid #666666;
      padding: 0 15px;
      font-size: 14px;
      &::placeholder {
        color: #666666;
      }
    }
    textarea {
      width: 100%;
      height: 120px;
      background: #E3E3E3;
      border: 1px solid #666666;
      padding: 15px;
      font-size: 14px;
      resize: none;
      &::placeholder {
        color: #666666;
      }
    }
    label {
      position: absolute;
      top: 50%;
      left: 15px;
      right: 15px;
      transform: translate(0, -50%);
      width: 100%;
      font-size: 14px;
      color: #666666;
      pointer-events: none;
      span {
        color: #992A28;
      }
    }
  }
  &-check {
    position: relative;
    display: flex;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
      }
    }
    label {
      position: relative;
      font-size: 14px;
      line-height: 160%;
      color: #666666;
      padding-left: 39px;
      &:after {
        content: '';
        position: absolute;
        top: -2px;
        left: 0;
        width: 24px;
        height: 24px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1px solid #666666;
        transition: all 0.3s;
      }
      a {
        position: relative;
        z-index: 3;
        &:hover {
          color: #DEA515;
        }
      }
    }
  }
  &-btn {
    padding-left: 30px;
    padding-right: 30px;
  }
}


@media screen and (max-width: 992px) {
  .text44 {
    font-size: 36px;
  }
  .fixed-item {
    display: none;
  }
}

@media screen and (max-width: 399px) {
  .text44 {
    font-size: 30px;
  }
}